"use client";

import { cn } from "@trading-platform/ui";

interface CircularProgressProps {
  value: number; // Number of registered items (0-max)
  max?: number; // Maximum spots (default 50)
  size?: number; // Size of the circle in pixels
  strokeWidth?: number; // Width of the stroke
  showValue?: boolean; // Whether to show the maximum waitlist spots in the center
  className?: string;
}

function CircularProgress({
  value,
  max = 50,
  size = 32,
  strokeWidth = 3,
  showValue = true,
  className,
}: CircularProgressProps) {
  const remaining = Math.max(0, max - value);
  // Show availability percentage (inverse of registration percentage)
  // When remaining = max (50), show 100% black (all spots available)
  // When remaining = 0, show 0% black (all spots taken)
  const availabilityPercentage = Math.min(
    100,
    Math.max(0, (remaining / max) * 100),
  );

  // SVG circle calculations
  const center = size / 2;
  const radius = (size - strokeWidth) / 2;
  const circumference = 2 * Math.PI * radius;
  // Calculate offset so that more remaining = more black ring
  const offset = circumference - (availabilityPercentage / 100) * circumference;

  return (
    <div
      className={cn("relative shrink-0", className)}
      style={{ width: size, height: size }}
    >
      <svg
        width={size}
        height={size}
        className="-rotate-90 absolute inset-0"
      >
        {/* Background circle (light gray) */}
        <circle
          cx={center}
          cy={center}
          r={radius}
          fill="none"
          stroke="currentColor"
          strokeWidth={strokeWidth}
          className="text-gray-300"
        />
        {/* Progress circle (black) */}
        <circle
          cx={center}
          cy={center}
          r={radius}
          fill="none"
          stroke="currentColor"
          strokeWidth={strokeWidth}
          strokeDasharray={circumference}
          strokeDashoffset={offset}
          strokeLinecap="round"
          className="text-black transition-all duration-300"
        />
      </svg>
      {/* Center text showing remaining spots */}
      {showValue && (
        <div
          className="absolute flex items-center justify-center"
          style={{
            left: 0,
            top: 0,
            width: size,
            height: size
          }}
        >
          <span className="text-sm font-semibold leading-none text-black">
            {remaining}
          </span>
        </div>
      )}
    </div>
  );
}

export { CircularProgress, type CircularProgressProps };
