// Waitlist configuration constants
export const WAITLIST_CONFIG = {
  maxSpots: 50,
} as const;

export interface WaitlistStats {
  registeredCount: number;
  remaining: number;
  maxSpots: number;
  isFull: boolean;
}

/**
 * Fetch waitlist stats from API
 */
export async function fetchWaitlistStats(): Promise<WaitlistStats> {
  try {
    const response = await fetch("/api/waitlist/stats", {
      cache: "no-store", // Always fetch fresh data
    });

    if (!response.ok) {
      const errorText = await response.text();
      console.error("Waitlist stats API error:", response.status, errorText);
      throw new Error(
        `Failed to fetch waitlist stats: ${response.status} ${response.statusText}`,
      );
    }

    const result = (await response.json()) as {
      success: boolean;
      data?: {
        maxSpots: number;
        registeredCount: number;
        remaining: number;
        isFull: boolean;
      };
    };

    if (result.success && result.data) {
      return {
        maxSpots: result.data.maxSpots,
        registeredCount: result.data.registeredCount,
        remaining: result.data.remaining,
        isFull: result.data.isFull,
      };
    }

    throw new Error("Invalid response format");
  } catch (error) {
    console.error("Error fetching waitlist stats:", error);
    // Return defaults on error so UI still works
    // This ensures the site doesn't break if API is unavailable
    return {
      maxSpots: WAITLIST_CONFIG.maxSpots,
      registeredCount: 0,
      remaining: WAITLIST_CONFIG.maxSpots,
      isFull: false,
    };
  }
}
