"use client";

import Image from "next/image";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { z } from "zod";
import { useState } from "react";

import { Button } from "@trading-platform/ui/button";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
} from "@trading-platform/ui/dialog";
import { Input } from "@trading-platform/ui/input";
import { Label } from "@trading-platform/ui/label";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@trading-platform/ui/select";
import { Textarea } from "@trading-platform/ui/textarea";

import { ASSETS } from "./constants";
import { useWaitlistStats } from "./use-waitlist-stats";

const reservationSchema = z.object({
  restaurantName: z.string().min(1, "Restaurant name is required"),
  numberOfLocations: z.string().min(1, "Number of locations is required"),
  yourName: z.string().min(1, "Your name is required"),
  email: z.string().email("Invalid email address"),
  phone: z.string().optional(),
  biggestChallenge: z.string().optional(),
});

type ReservationFormData = z.infer<typeof reservationSchema>;

interface ReservationDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
}

export function ReservationDialog({
  open,
  onOpenChange,
}: ReservationDialogProps) {
  const { stats } = useWaitlistStats();
  const [isSubmitting, setIsSubmitting] = useState(false);

  const {
    register,
    handleSubmit,
    formState: { errors },
    setValue,
    watch,
    reset,
  } = useForm<ReservationFormData>({
    resolver: zodResolver(reservationSchema),
  });

  const numberOfLocations = watch("numberOfLocations");

  const onSubmit = async (data: ReservationFormData) => {
    setIsSubmitting(true);
    try {
      // TODO: Implement API call to submit the form
      console.log("Submitting form:", data);
      // Simulate API call
      await new Promise((resolve) => setTimeout(resolve, 1000));
      reset();
      onOpenChange(false);
    } catch (error) {
      console.error("Error submitting form:", error);
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="max-w-[690px] border-[#ffffff33] bg-[#0c0c0c] p-0">
        <div className="flex h-[769px] w-full">
          {/* Left column: Info */}
          <div className="flex w-[256px] flex-col gap-16 border-r border-white/20 bg-gradient-to-b from-white/10 to-white/5 p-8">
            <div className="flex flex-1 flex-col gap-8">
              <div className="flex flex-col gap-4">
                <div className="flex h-8 w-8 items-center justify-center rounded-lg bg-white">
                  <Image
                    src={ASSETS.icon}
                    alt="trading-platform"
                    width={24}
                    height={24}
                    unoptimized
                  />
                </div>
                <h3 className="w-[192px] whitespace-pre-wrap text-[30px] leading-[36px] tracking-[0.3955px] text-zinc-300">
                  Limited Early Access
                </h3>
                <p className="w-[192px] whitespace-pre-wrap text-base leading-[26px] tracking-[-0.3125px] text-[#9f9fa9]">
                  Join the first 50 restaurants to shape the future of restaurant
                  operations.
                </p>
              </div>

              <div className="flex flex-col gap-3">
                {/* Value items */}
                <div className="flex gap-3">
                  <div className="flex h-6 w-6 shrink-0 items-center justify-center rounded-full border border-white/20 bg-white/10">
                    <Image
                      src={ASSETS.dialogCheckIcon}
                      alt="Check"
                      width={14}
                      height={14}
                      unoptimized
                    />
                  </div>
                  <div>
                    <p className="text-sm font-normal leading-5 text-white">
                      Value 1
                    </p>
                    <p className="mt-0.5 text-xs leading-4 text-[#71717b]">
                      Description
                    </p>
                  </div>
                </div>
                <div className="flex gap-3">
                  <div className="flex h-6 w-6 shrink-0 items-center justify-center rounded-full border border-white/20 bg-white/10">
                    <Image
                      src={ASSETS.dialogCheckIcon}
                      alt="Check"
                      width={14}
                      height={14}
                      unoptimized
                    />
                  </div>
                  <div>
                    <p className="text-sm font-normal leading-5 text-white">
                      Value 2
                    </p>
                    <p className="mt-0.5 text-xs leading-4 text-[#71717b]">
                      Description
                    </p>
                  </div>
                </div>
                <div className="flex gap-3">
                  <div className="flex h-6 w-6 shrink-0 items-center justify-center rounded-full border border-white/20 bg-white/10">
                    <Image
                      src={ASSETS.dialogCheckIcon}
                      alt="Check"
                      width={14}
                      height={14}
                      unoptimized
                    />
                  </div>
                  <div>
                    <p className="text-sm font-normal leading-5 text-white">
                      Value 3
                    </p>
                    <p className="mt-0.5 text-xs leading-4 text-[#71717b]">
                      Description
                    </p>
                  </div>
                </div>
              </div>
            </div>

            {/* Spots remaining */}
            <div className="flex flex-col gap-4">
              <div className="flex h-8 items-center justify-between">
                <Label className="text-sm font-medium leading-5 tracking-[-0.1504px] text-[#d9d9d9]">
                  Spots Remaining
                </Label>
                <div className="relative h-8 w-8">
                  <Image
                    src={ASSETS.dialogValueIcon}
                    alt="Spots"
                    fill
                    className="object-contain"
                    unoptimized
                  />
                  <span className="absolute left-2 top-1/2 -translate-y-1/2 text-xs font-medium leading-4 text-white">
                    {stats.registeredCount}
                  </span>
                </div>
              </div>
              <div className="rounded-[10px] border border-white/10 bg-gradient-to-b from-white/10 to-white/5 p-4">
                <div className="flex items-start gap-2.5">
                  <Image
                    src={ASSETS.dialogReminderIcon}
                    alt="Info"
                    width={14}
                    height={14}
                    className="mt-0.5 shrink-0"
                    unoptimized
                  />
                  <p className="flex-1 whitespace-pre-wrap text-xs leading-4 text-[#9f9fa9]">
                    Early access closes when all 50 spots are filled.
                  </p>
                </div>
              </div>
            </div>
          </div>

          {/* Right column: Form */}
          <div className="flex flex-1 flex-col bg-gradient-to-b from-white/10 to-white/5 p-8">
            <form onSubmit={handleSubmit(onSubmit)} className="flex h-full flex-col">
              <DialogHeader className="mb-6 text-left">
                <DialogTitle className="text-xl font-normal leading-7 tracking-[-0.4492px] text-white">
                  Your Information
                </DialogTitle>
                <p className="text-sm font-normal leading-5 tracking-[-0.1504px] text-[#9f9fa9]">
                  Complete the form to secure your spot
                </p>
              </DialogHeader>

              <div className="flex-1 overflow-y-auto">
                <div className="flex flex-col gap-9">
                  {/* Restaurant Name */}
                  <div className="flex flex-col gap-2">
                    <Label className="text-sm font-medium leading-5 tracking-[-0.1504px] text-zinc-300">
                      Restaurant Name *
                    </Label>
                    <Input
                      {...register("restaurantName")}
                      placeholder="Enter restaurant name"
                      className="h-11 rounded-lg border border-white/20 bg-black/25 px-3 py-1 text-sm font-normal leading-normal text-zinc-300 placeholder:text-[#71717b]"
                    />
                    {errors.restaurantName && (
                      <p className="text-xs text-red-500">
                        {errors.restaurantName.message}
                      </p>
                    )}
                  </div>

                  {/* Number of Locations */}
                  <div className="flex flex-col gap-2">
                    <Label className="text-sm font-medium leading-5 tracking-[-0.1504px] text-zinc-300">
                      Number of Locations *
                    </Label>
                    <Select
                      value={numberOfLocations}
                      onValueChange={(value) => setValue("numberOfLocations", value)}
                    >
                      <SelectTrigger className="h-9 rounded-lg border border-white/20 bg-black/25">
                        <SelectValue placeholder="Select..." />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="1">1 location</SelectItem>
                        <SelectItem value="2-3">2-3 locations</SelectItem>
                        <SelectItem value="4-5">4-5 locations</SelectItem>
                        <SelectItem value="6-10">6-10 locations</SelectItem>
                        <SelectItem value="11+">11+ locations</SelectItem>
                      </SelectContent>
                    </Select>
                    {errors.numberOfLocations && (
                      <p className="text-xs text-red-500">
                        {errors.numberOfLocations.message}
                      </p>
                    )}
                  </div>

                  {/* Your Name */}
                  <div className="flex flex-col gap-2">
                    <Label className="text-sm font-medium leading-5 tracking-[-0.1504px] text-zinc-300">
                      Your Name *
                    </Label>
                    <Input
                      {...register("yourName")}
                      placeholder="Enter your name"
                      className="h-11 rounded-lg border border-white/20 bg-black/25 px-3 py-1 text-sm font-normal leading-normal text-zinc-300 placeholder:text-[#71717b]"
                    />
                    {errors.yourName && (
                      <p className="text-xs text-red-500">
                        {errors.yourName.message}
                      </p>
                    )}
                  </div>

                  {/* Email and Phone */}
                  <div className="flex gap-4">
                    <div className="flex flex-1 flex-col gap-2">
                      <Label className="text-sm font-medium leading-5 tracking-[-0.1504px] text-zinc-300">
                        Email *
                      </Label>
                      <Input
                        {...register("email")}
                        type="email"
                        placeholder="joe@restaurant.com"
                        className="h-11 rounded-lg border border-white/20 bg-black/25 px-3 py-1 text-sm font-normal leading-normal text-zinc-300 placeholder:text-[#71717b]"
                      />
                      {errors.email && (
                        <p className="text-xs text-red-500">
                          {errors.email.message}
                        </p>
                      )}
                    </div>
                    <div className="flex flex-1 flex-col gap-2">
                      <Label className="text-sm font-medium leading-5 tracking-[-0.1504px] text-zinc-300">
                        Phone
                      </Label>
                      <Input
                        {...register("phone")}
                        type="tel"
                        placeholder="+1 (555) 123-4567"
                        className="h-11 rounded-lg border border-white/20 bg-black/25 px-3 py-1 text-sm font-normal leading-normal text-zinc-300 placeholder:text-[#71717b]"
                      />
                    </div>
                  </div>

                  {/* Biggest Challenge */}
                  <div className="flex flex-col gap-2">
                    <Label className="text-sm font-medium leading-5 tracking-[-0.1504px] text-zinc-300">
                      Biggest Challenge (Optional)
                    </Label>
                    <Textarea
                      {...register("biggestChallenge")}
                      placeholder="Tell us about your main operational challenge..."
                      className="min-h-[80px] rounded-lg border border-white/20 bg-black/25 px-3 py-2 text-sm font-normal leading-5 tracking-[-0.1504px] text-zinc-300 placeholder:text-[#71717b]"
                    />
                  </div>
                </div>
              </div>

              {/* Submit Button */}
              <div className="border-t border-white/10 pt-4">
                <Button
                  type="submit"
                  disabled={isSubmitting}
                  className="h-11 w-full rounded-lg border border-white/20 bg-gradient-to-r from-white to-[#f4f4f5] text-sm font-medium leading-5 tracking-[-0.1504px] text-black hover:from-white/90 hover:to-[#f4f4f5]/90 disabled:opacity-50"
                >
                  {isSubmitting ? "Submitting..." : "Submit Application"}
                </Button>
              </div>
            </form>
          </div>
        </div>
      </DialogContent>
    </Dialog>
  );
}

