"use client";

import { useQuery } from "@tanstack/react-query";

import type { WaitlistStats } from "./waitlist-constants";
import { fetchWaitlistStats, WAITLIST_CONFIG } from "./waitlist-constants";

/**
 * React Query key for waitlist stats
 */
export const WAITLIST_STATS_QUERY_KEY = ["waitlist", "stats"] as const;

/**
 * Hook to fetch and manage waitlist statistics using React Query
 */
export function useWaitlistStats() {
  const {
    data: stats,
    isLoading,
    error,
    isFetching,
  } = useQuery<WaitlistStats>({
    queryKey: WAITLIST_STATS_QUERY_KEY,
    queryFn: fetchWaitlistStats,
    // Use default values on error
    placeholderData: {
      registeredCount: 0,
      remaining: WAITLIST_CONFIG.maxSpots,
      maxSpots: WAITLIST_CONFIG.maxSpots,
      isFull: false,
    },
    // Refetch options are handled by QueryClient defaultOptions
    staleTime: 30000, // Data is fresh for 30 seconds
    refetchInterval: 30000, // Auto-refetch every 30 seconds
    // Prevent refetch on mount to avoid hydration issues
    refetchOnMount: false,
    // Only refetch after window focus if data is stale
    refetchOnWindowFocus: false,
    // Retry once on failure
    retry: 1,
    // Use default values while loading to prevent hydration mismatch
    initialData: {
      registeredCount: 0,
      remaining: WAITLIST_CONFIG.maxSpots,
      maxSpots: WAITLIST_CONFIG.maxSpots,
      isFull: false,
    },
  });

  // Stats will always be defined due to initialData and placeholderData
  return {
    stats,
    isLoading,
    isFetching,
    error: error ?? null,
  };
}
